<template>
  <div>
    <main class="app">
      <section class="greeting">
        <h2 class="title">
          What's up, <input type="text" placeholder="Name here" v-model="name" />
        </h2>

        <section class="create-todo">
          <h3>CREATE A TODO</h3>
          <form @submit.prevent="addTodo()">
            <h4>wats's on your todo list?</h4>
            <input
              type="text"
              placeholder="e.g. make a video"
              v-model="input_content"
            />
            <h4>Pick a category</h4>
            <div class="options">
              <label for="category1">
                <input
                  type="radio"
                  name="category"
                  id="category1"
                  value="Study"
                  v-model="input_category"
                />
                <span class="bubble study"></span>
                <div>Study</div>
              </label>
              <label for="category2">
                <input
                  type="radio"
                  name="category"
                  id="category2"
                  value="rotin"
                  v-model="input_category"
                />
                <span class="bubble rotin"></span>
                <div>Rotin</div>
              </label>
            </div>
            <input type="submit" value="Add todo" />
          </form>
        </section>
      </section>
      <section class="todo-list">
        <h3>TODO LIST</h3>
        <div class="list">
          <!-- <div v-for="todo in todos" :key="todo.createAt">
            {{ todo.category }}
          </div> -->
          <div
            v-for="todo in todos"
            :class="`todo-item ${todo.done && 'done'}`"
            :key="todo.createAt"
          >
            <label>
              <input type="checkbox" v-model="todo.done" />
              <span :class="`bubble ${todo.category}`"></span>
            </label>
            <div class="todo-content">
              <input type="text" v-model="todo.content" />
            </div>
            <div class="actions">
              <button class="delete" @click="removeTodo(todo)">Delete</button>
            </div>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
// import { json } from "express";
import { onMounted, ref, watch } from "vue";
// import { ref, onMounted, computed, watch } from "vue";
export default {
  setup() {
    let todos = ref([]);
    let name = ref("");
    let input_content = ref("");
    let input_category = ref(null);

    let addTodo = () => {
      // if (input_content.value.trim() === "") {
      // return;
      // } else {
      todos.value.push({
        content: input_content.value,
        category: input_category.value,
        createAt: new Date().getTime(),
        done: false,
      });
      input_content.value = "";
      input_category.value = "";
      console.log("good");
      // }
    };
    let removeTodo = (todo) => {
      todos.value = todos.value.filter((t) => {
        t !== todo;
      });
    };

    watch(
      todos,
      (newValue) => {
        localStorage.setItem("todos", JSON.stringify(newValue));
      },
      { deep: true }
    );
    watch(name, (newValue) => {
      localStorage.setItem("name", newValue);
    });
    onMounted(() => {
      name.value = localStorage.getItem("name") || "";
      todos.value = JSON.parse(localStorage.getItem("todos") || []);
    });
    return {
      todos,
      name,
      input_content,
      input_category,
      addTodo,
      removeTodo,
    };
  },
  components: {},
  data() {
    return {};
  },
  methods: {},
  computed: {},
  watch: {},
  provide() {
    return {};
  },
};
</script>
